/* back-to-top rocket*/
@media print, screen and (min-width: 48em) {
#rocket {
    position: fixed;
    right: 50px;
    bottom: 50px;
    display: block;
    visibility: hidden;
    width: 26px;
    height: 48px;
    background: url("data:image/png;base64,") no-repeat 50% 0;
    opacity: 0;
    -webkit-transition: visibility 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335), opacity 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335), -webkit-transform 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335);
    transition: visibility 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335), opacity 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335), -webkit-transform 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335);
    transition: visibility 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335), opacity 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335), transform 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335);
    transition: visibility 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335), opacity 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335), transform 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335), -webkit-transform 0.6s cubic-bezier(0.6, 0.04, 0.98, 0.335);
}
#rocket i {
    display: block;
    margin-top: 48px;
    height: 14px;
    background: url("data:image/png;base64,") no-repeat 50% -48px;
    opacity: .5;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    -webkit-transform-origin: 50% 0;
    transform-origin: 50% 0;
}
#rocket:hover {
    background-position: 50% -62px;
}
#rocket:hover i {
    background-position: 50% 100%;
    -webkit-animation: flaming .7s infinite;
    animation: flaming .7s infinite;
}
#rocket.show {
    visibility: visible;
    opacity: 1;
}
#rocket.launch {
    background-position: 50% -62px;
    opacity: 0;
    -webkit-transform: translateY(-500px);
    transform: translateY(-500px);
    pointer-events: none;
}
#rocket.launch i {
    background-position: 50% 100%;
    -webkit-transform: scale(1.4, 3.2);
    transform: scale(1.4, 3.2);
}
}

