}
#process .timeline-centered .timeline-entry .timeline-entry-inner {
  position: relative;
  margin-left: -20px;
}
#process .timeline-centered .timeline-entry .timeline-entry-inner::before,
#process .timeline-centered .timeline-entry .timeline-entry-inner::after {
