  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 9px 9px 9px 0;
  border-color: transparent #eee transparent transparent;
  left: 0;
  top: 50%;
  margin-top: -9px;
  margin-left: -9px;
}
#process .line {
  position: absolute;
  display: block;
  width: 4px;
  background: #eee;
  top: -3%;
  right: -30px;
  bottom: -3%;
}
#process .present,
#process .born {
  font-size: 14px;
  font-family: 'Georgia', serif, Helvetica, "Helvetica Neue", "Hiragino Sans GB", "Microsoft YaHei", "Source Han Sans CN", "WenQuanYi Micro Hei", Arial, sans-serif;
  font-style: italic;
  color: #333;
  padding: 10px;
  background-color: #eee;
  border-radius: 3px;
}
#process .present::after,
#process .born::after {
  left: 100%;
  top: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  border-color: rgba(136, 183, 213, 0);
  border-left-color: #eee;
  border-width: 10px;
  margin-top: -10px;
}
#process .present {
  position: absolute;
  top: -3%;
  right: 0;
  margin-top: -20px;
  line-height: 100%;
}
#process .born {
  position: absolute;
  bottom: -3%;
  right: 0;
  margin-bottom: -20px;
  line-height: 100%;
}
#process .dot_tp {
  position: absolute;
  top: -3%;
  right: -35px;
  background-color: transparent;
  height: 15px;
  width: 15px;
  margin-top: -13px;
  border-radius: 50%;
  border: 3px solid #eee;
}
#process .dot_bt {
  position: absolute;
  bottom: -3%;
  right: -35px;
  background-color: transparent;
  height: 15px;
  width: 15px;
  margin-bottom: -13px;
  border-radius: 50%;
  border: 3px solid #eee;
}
@media (max-width: 768px) {
#process .line {
    right: 10px;
}
#process .timeline-centered .timeline-entry .timeline-entry-inner .timeline-label {
    margin-right: 30px;
    margin-left: 45px;
    padding: 20px;
}
#process .timeline-centered .timeline-entry {
    margin-right: 20px;
}
#process .dot_tp,
  #process .dot_bt {
    right: 5px;
}
#process .present,
  #process .born {
    right: 35px;
}
}

/* syntax highlight*/
figure.highlight,
.codeblock {
  background: #f7f8f8;
  margin: 10px 0;
  line-height: 1.1em;
  color: #333;
  padding-top: 15px;
  overflow: hidden;
}
figure.highlight table,
.codeblock table {
  display: block;
  width: 100%;
}
figure.highlight pre,
figure.highlight .gutter,
figure.highlight .code,
figure.highlight .tag,
.codeblock pre,
.codeblock .gutter,
.codeblock .code,
.codeblock .tag {
  background-color: inherit;
  font-family: Menlo, Consolas, monospace;
  border: none;
  padding: 0;
  margin: 0;
  cursor: text;
}
